import {graphql} from 'gatsby';
import Img from 'gatsby-image';
import React from 'react';

import {WillAnimate} from '../../components/content_animate';
import {Layout} from '../../components/layout';
import {ScrollTo} from '../../components/scroll_to';

export default ({data}: any) => (
    <Layout
        title="Contact – Enschede en Amsterdam – Samen softwareproducten creëren?"
        description="Nieuwe softwareproductontwikkelingsopdracht? Neem contact op met ons kantoor in Enschede of Amsterdam. Ook als je een stap wilt maken in je carrière horen we graag van je."
        ogImage="contact.png"
        translatedPagePath="/en/contact/">
        <div id="hero" className="hero hero--contact">
            <div className="hero__content-wrapper">
                <div className="hero__content">
                    <h2>
                        Nieuwe <span className="highlight">opdracht</span>?<br /> Bel of e-mail{' '}
                        <span className="inline-avatar">
                            <Img alt="Joris" fixed={data.joris.childImageSharp.fixed} Tag="span" />
                            <span>Joris</span>
                        </span>{' '}
                        via <a href="tel:+31537113499">053&nbsp;711&nbsp;34&nbsp;99</a> of{' '}
                        <a href="mailto:joris@305.nl">joris@305.nl</a> <br />
                        <br />
                        Stap in je <span className="highlight">carrière</span>? Bekijk onze{' '}
                        <a
                            title="Carrière website (opent in een nieuwe tab)"
                            target="_blank"
                            href="https://careers.305.nl/?lang=nl"
                            rel="noreferrer">
                            vacatures en stages
                        </a>
                        ,<br className="br-contact-hide" /> of vraag{' '}
                        <span className="inline-avatar">
                            <Img alt="Thijs" fixed={data.thijs.childImageSharp.fixed} Tag="span" />
                            <span>Thijs</span>
                        </span>{' '}
                        via <a href="tel:+31202614749">020&nbsp;261&nbsp;47&nbsp;49</a> of{' '}
                        <a href="mailto:thijs@305.nl">thijs@305.nl</a>
                    </h2>
                </div>
            </div>
            <div className="hero__locations-wrapper">
                <div className="hero__column">
                    <ScrollTo
                        target="/nl/contact/#enschede"
                        className="hero__location hero__location--inline"
                        title="Contactgegevens Enschede">
                        Enschede
                    </ScrollTo>
                    <ul className="list">
                        <li className="list__item">
                            Rigtersbleek-Zandvoort 10-2.06 <br /> 7521BE Enschede
                        </li>
                    </ul>
                </div>
                <div className="hero__column">
                    <ScrollTo
                        target="/nl/contact/#amsterdam"
                        className="hero__location hero__location--inline"
                        title="Contactgegevens Amsterdam">
                        Amsterdam
                    </ScrollTo>
                    <ul className="list">
                        <li className="list__item">
                            Kruithuisstraat 13 <br /> 1018WJ Amsterdam
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <section className="section">
            <WillAnimate as="article" id="enschede" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image">
                                <div className="image__content">
                                    <Img alt="Kantoor Enschede" fluid={data.enschede.childImageSharp.fluid} />
                                </div>
                            </div>
                        </div>
                        <div className="content__copy">
                            <h2>Enschede</h2>
                            <p>
                                Ons hoofdkantoor is gevestigd in het authentieke gebouw{' '}
                                <span className="highlight">De Spinnerij</span> op de Rigtersbleek, vlakbij{' '}
                                Twekkelerveld.
                            </p>
                            <p>
                                Neem afslag Enschede-West op de A35 en volg de borden Rigtersbleek. Of stap uit op het{' '}
                                treinstation Enschede en neem de bus naar bushalte Lambertus Buddestraat.
                            </p>
                            <ul className="list list--links">
                                <li className="list__item">
                                    <a
                                        title="Google Maps"
                                        href="https://www.google.com/maps/place/Label305+Enschede/@52.2224308,6.8697578,17z/data=!3m1!4b1!4m5!3m4!1s0x47b813da389ae993:0x2b88f1b9f0b67b68!8m2!3d52.2224275!4d6.8719465">
                                        Rigtersbleek-Zandvoort 10-2.06 <br /> 7521BE Enschede
                                    </a>
                                </li>
                                <li className="list__item list__item--divider">
                                    <a title="Bel onze locatie in Enschede" href="tel:+31537113499">
                                        +31 (0)53 711 34 99
                                    </a>
                                </li>
                                <li className="list__item">
                                    <a title="E-mail onze locatie in Enschede" href="mailto:hi@305.nl">
                                        hi@305.nl
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" id="amsterdam" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--left" />
                        <div className="content__image content__image--right">
                            <div className="image">
                                <div className="image__content">
                                    <Img alt="Kantoor amsterdam" fluid={data.amsterdam.childImageSharp.fluid} />
                                </div>
                            </div>
                        </div>
                        <div className="content__copy">
                            <h2>Amsterdam</h2>
                            <p>
                                Ongeveer 10 minuten fietsen vanaf station Amsterdam Centraal is een deel van ons team{' '}
                                gevestigd op het terrein van <span className="highlight">de Werf ‘t&nbsp;Kromhout</span>{' '}
                                in de Kadijkenbuurt vlakbij <span className="highlight">Artis</span>.
                            </p>
                            <p>
                                Neem afslag Zeeburg op de A10 en volg de borden richting Artis. Er is beperkte{' '}
                                parkeerruimte op de werf.
                            </p>
                            <p>
                                Met het OV? Stap uit op station Amsterdam Centraal en neem bus 22 naar bushalte{' '}
                                Wittenburgergracht. Loop vervolgens over de Pelikaanbrug naar de werf.
                            </p>
                            <ul className="list list--links">
                                <li className="list__item">
                                    <a
                                        title="Google Maps"
                                        href="https://www.google.com/maps/place/Label305+Amsterdam/@52.367965,4.9187692,17z/data=!3m1!4b1!4m5!3m4!1s0x47c609a07c093bbd:0x7b559c2e666f5c0!8m2!3d52.3679617!4d4.9209579">
                                        Kruithuisstraat 13 <br /> 1018WJ Amsterdam
                                    </a>
                                </li>
                                <li className="list__item list__item--divider">
                                    <a title="Bel onze locatie in Amsterdam" href="tel:+31202614749">
                                        +31 (0)20 261 47 49
                                    </a>
                                </li>
                                <li className="list__item">
                                    <a title="E-mail onze locatie in Amsterdam" href="mailto:hi@305.amsterdam">
                                        hi@305.amsterdam
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </WillAnimate>
        </section>
    </Layout>
);

export const query = graphql`
    query {
        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }
        thijs: file(relativePath: {eq: "people/thijs_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }

        enschede: file(relativePath: {eq: "office/enschede-chill.jpg"}) {
            childImageSharp {
                fluid(maxWidth: 700) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        amsterdam: file(relativePath: {eq: "office/amsterdam.jpg"}) {
            childImageSharp {
                fluid(maxWidth: 700) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
    }
`;
